 datarifa() {

     let re = '0';
     const currentDate = new Date();
  let sifestivo = 0;
  let sifestivo2 = 0;
  for (let i = 0; i < this.vdiasfestivos.length; i++) {


         if ((currentDate.getDate() + '-' + (currentDate.getMonth() + 1) + '-' + currentDate.getFullYear()) === this.vdiasfestivos[i]) {
         re = '0';
         sifestivo = 1;
         break;
         }
     }


     if (sifestivo === 0) {


      const weekdays = ['Domingo', 'Lunes', 'Martes', 'Miercoles', 'Jueves', 'Viernes', 'Sabado'];
      const dia = weekdays[currentDate.getDay()];

      for (let i = 0; i < this.vtarifa.length; i++) {

              const ttar = (this.vtarifa[i].nombre).split(',');



                if (dia === ttar[0]) {// encuentra el dia

                 //   System.out.println(dahoraactual());
                const horat1 = Number(ttar[1]);
                const horat2 = Number(ttar[2]);
                const horatm = Number(currentDate.getHours());
                const tarifa1 = Number(ttar[3]);


                if (horatm >= horat1 && horatm <= horat2) {
                  re = ttar[3];
                 
                  this.diat=dia;
                  this.horat=horatm
                   sifestivo2 = 1;

                }

                if (sifestivo2 === 1) {
                break;
                }



                }
            }

     }


 return re;
 }


